<template>
  <div class="page-body">
    <van-nav-bar
      fixed
      title="账单详情"
      left-arrow
      @click-left="$router.back()"
    ></van-nav-bar>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <div class="log-list">
        <div v-for="(item, index) in log" :key="index" class="log-item">
          <div class="item-left flex-box">
            <div class="rec-status">
              <span>{{ item.describe }}</span>
            </div>
            <div class="rec-time">
              <span>{{ item.create_time }}</span>
            </div>
          </div>
          <div class="item-right">
            <span>{{ item.money > 0 ? "+" : "" }}{{ item.money }}元</span>
          </div>
        </div>
      </div>
    </van-list>
  </div>
</template>

<script>
import { getBalanceLogApi } from '@/api/wallet'

export default {
  name: 'BalanceLog',

  data () {
    return {
      loading: false,
      finished: false,

      page: 1,
      log: []
    }
  },

  methods: {
    async onLoad () {
      await this.getBalanceLog()
      this.page++
      this.loading = false
    },

    async getBalanceLog () {
      const { data: { list } } = await getBalanceLogApi(this.page)
      this.log = this.log.concat(list.data)
      if (this.log.length >= list.total) {
        this.finished = true
      }
    }

  }
}
</script>

<style lang="less" scoped>
.page-body {
  padding: 48px 0;
  background: #fff;
}

.log-list {
  padding: 0 15px;
}

.log-item {
  font-size: 14px;
  padding: 10px 10px;
  line-height: 1.8;
  border-bottom: 1px solid rgb(238, 238, 238);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.rec-status {
  color: #333;

  .rec-time {
    color: rgb(160, 160, 160);
    font-size: 13px;
  }
}
</style>
